<template>
  <div class="login">
    <s-header :name="type == 'login' ? '' : ''" :back="'/home'"></s-header>
    <img class="logo" src="//s.weituibao.com/1582958061265/mlogo.png" alt />
    <div v-if="type == 'login'" class="login-body login">
      <van-form @submit="onSubmit">
        <van-field
          v-model="username"
          name="username"
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="password"
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }]"
        />
        <div class="verify">
          <Verify
            ref="loginVerifyRef"
            @error="error"
            :showButton="false"
            @success="success"
            :width="'100%'"
            :height="'40px'"
            :fontSize="'16px'"
            :type="2"
          ></Verify>
        </div>
        <div style="margin: 16px;">
          <div class="link-register" @click="toggle('register')"></div>
          <van-button round block type="info" color="#1baeae" native-type="submit"></van-button>
        </div>
      </van-form>
    </div>
    <div v-else class="login-body register">
      <van-form @submit="onSubmit">
        <van-field
          v-model="username1"
          name="username1"
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }]"
        />
        <van-field
          v-model="password1"
          type="password"
          name="password1"
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }]"
        />
        <div class="verify">
          <Verify
            ref="loginVerifyRef"
            @error="error"
            :showButton="false"
            @success="success"
            :width="'100%'"
            :height="'40px'"
            :fontSize="'16px'"
            :type="2"
          ></Verify>
        </div>
        <div style="margin: 16px;">
          <div class="link-login" @click="toggle('login')"></div>
          <van-button round block type="info" color="#1baeae" native-type="submit"></van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

